$primary: #33C7EF;
$secondary: #37F093;
$container: 960px;
html {
  font: normal normal normal 1rem/1.6 'Nunito', -apple-system, BlinkMacSystemFont, Helvetica Neue, Helvetica, Arial, sans-serif;
  color: #2d2d2d;
  height: 100%;
  -webkit-font-smoothing: antialiased;
}

body {
  margin: 0;
}

img {
  height: auto;
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5 {
  line-height: 1.2;
  margin-top: 0;
  margin-bottom: 1rem;
  font-weight: 400;
  font-family: 'Paytone One';
}

a {
  color: $primary;
  font-weight: 700;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}

.container {
  max-width: $container;
  padding: 0 1rem;
  margin: 0 auto;
}

.flex {
  display: flex;
}

.ac {
  align-items: center;
}

.sb {
  justify-content: space-between;
}

.code {
  margin: auto;
}

pre {
  background: #0D0D0D;
  color: #d5d5d5;
  font-family: Menlo, Roboto Mono, Courier New, monospace;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  line-height: 1.6;
  font-weight: 400;
  tab-size: 2;
  border-radius: 0;
  margin: 1.5rem auto;
  padding: 2rem;
  border-radius: 6px;
  border-top: 20px solid #d0d0d0;
  -webkit-font-smoothing: subpixel-antialiased;
  box-shadow: 2px 3px 30px rgba(0, 0, 0, .15);
}

code {
  font-family: Menlo, Roboto Mono, Courier New, monospace;
}

.hero-container {
  padding: 80px 0;
  max-width: 550px;
  margin-right: 50px;
  h1 {
    margin-bottom: 0;
    font-weight: 700;
    font-size: 3rem;
  }
  p {
    font-size: 1.3rem;
    color: #666;
    margin-bottom: 3rem;
  }
  .subtitle {
    font-weight: 700;
    font-size: 2rem;
  }
}

a.button,
a.button-secondary {
  display: inline-block;
  -webkit-appearance: none;
  transition: background 500ms ease;
  background-image: linear-gradient(to right, $primary 0%, $secondary 51%, $primary 100%);
  background-size: 300% auto; //background: linear-gradient(to top right, $primary 80%, $secondary 20%);
  color: white; //border: 6px solid white;
  border-radius: 40px;
  text-align: center;
  vertical-align: middle;
  padding: 1.25rem 1.75rem;
  font-size: 1.1rem;
  box-shadow: 2px 3px 5px rgba(0, 0, 0, .05);
  &:hover {
    text-decoration: none;
    background-position: right center;
  }
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: $container;
  margin: 0 auto;
  height: 80px;
  font-size: 1.1rem;
}

.brand {
  a {
    font-weight: 400;
    color: #2d2d2d; //background: linear-gradient(180deg, transparent 55%, lighten($primary, 30%) 0);
    text-decoration: none;
    &:hover {
      background: linear-gradient(180deg, transparent 55%, lighten($secondary, 30%) 0);
    }
  }
}

.site-navigation {
  height: 80px;
  padding: 0 1rem;
  border-bottom: 1px solid #efefef;
  font-family: 'Nunito';
  .menu a {
    color: #888;
    font-weight: 400;
    margin: 0 2px;
    padding: 1rem;
    &:hover {
      text-decoration: none;
      color: #111;
    }
  }
}

.site-footer {
  padding: 80px 0;
  background: #0D0D0D url('../nobuo2.png') no-repeat right top / contain;
  color: #dedede;
  h1,
  h2 {
    font-family: 'Nunito';
    color: #dedede;
    font-size: 1.8rem;
  }
  h1 {
    display: inline-block;
    //background: linear-gradient(180deg, transparent 85%, $primary 0);
    &:hover {
    }
  }
  h2 {
    background: linear-gradient(180deg, transparent 85%, $secondary 0);
    &:hover {
    }
  }
  .sub {
    color: $primary;
    font-weight: 700;
    font-size: 1.2rem;
  }
}

.about {
  max-width: 500px;
}

.boolean {
  color: lighten($primary, 0%);
}

.string {
  color: lighten($secondary, 10%);
}

.social-icons {
  margin-top: 2rem;
  a {
    color: white;
    margin: 0 5px;
    &:hover {
      text-decoration: none;
      color: lighten($primary, 10%);
    }
  }
}

.primary,
.secondary {
  // font-weight: 700;
}

.primary {
  color: $primary;
}

.secondary {
  color: $secondary;
}